
.strate{transition:.4s all;width:600px;height:600px;position:absolute;top:0;left:0;transform:translateZ(0px);background-color:transparent
}
.strate:not(.strate--map){background-color:rgba(0,0,0,0.03)
}
.strate:not(.strate--map) .leaflet-tile-pane{opacity:0
}
.leaflet-container{background-color:transparent !important
}
.leaflet-control-zoom{display:none
}
.leaflet-control-container{display:none
}



