
.credit{position:absolute;top:40px;right:0;width:400px;height:calc(100vh - 80px)
}
.credit-panel__button{height:40px;width:40px;right:0;position:absolute;right:60px;top:20px;background-image:url("./../assets/close.png");background-repeat:no-repeat;background-size:70%;background-position:center;background-color:transparent;border:0
}
.credit__block{padding-top:100px;text-align:left;padding-left:80px
}
.credit__link{text-decoration:none;color:black
}
.credit__title{margin-top:0
}
.credit__button{width:40px;height:calc(100vh - 80px);background-color:black;color:white;position:absolute;left:0;top:0;border:0
}
.credit__button span{transform:rotate(-90deg);display:inline-block;top:50%;position:relative;left:-18px;font-size:24px;font-weight:400
}
.credit--display{transform:translateX(0);height:100%;transition:.4s ease
}
.credit--hide{transform:translateX(360px);height:100%;transition:.4s ease
}



