
.list-enter-active,.list-leave-active{transition:all 1s
}
.list-enter,.list-leave-to{transform:translateX(-100%)
}
.infos{position:fixed;bottom:0;left:0;z-index:999;bottom:98px;border-top:0;padding:0;width:350px
}
.info{padding:20px;border:2px solid black
}
.info__title{text-transform:uppercase;font-size:24px;margin:0
}
.info--white{border-bottom:0px solid black;background-color:white
}
.info__value{font-weight:bold;padding-top:5px;font-size:24px;margin:0
}
.info__mention{color:black;text-transform:uppercase;margin-top:5px;display:block;font-size:18px
}
.info__year{float:left;background-color:black;color:white;padding:20px;font-size:40px;letter-spacing:0px;margin:0;width:60%;text-align:center;line-height:25px;padding-top:25px;height:70px
}
.info__temperature{float:left;margin:0;padding:25px 0;width:40%;text-align:center;font-size:20px;font-weight:bold;border-right:2px solid black;border-top:2px solid black;height:70px;color:#dd2d2d;background-color:white
}
@media screen and (max-width: 1360px){
.infos{width:230px;bottom:78px
}
.info{padding:15px 20px
}
.info__title,.info__value{font-size:18px
}
.info__year{font-size:30px;width:50%
}
.info__temperature{width:50%
}
.info__mention{font-size:13px
}
}



