
.clouds{position:relative
}
.clouds .cloud{background-color:transparent;height:200px;width:600px;background-repeat:no-repeat;background-size:300px;background-position:center;transition:all 10s;position:absolute;top:0;left:0;opacity:0.6
}
.clouds .cloud--1{background-image:url("./../assets/nuage1.png")
}
.clouds .cloud--1.cloud-state--1{transform:translateY(11px) translateX(30px) rotate(4deg)
}
.clouds .cloud--1.cloud-state--2{transform:translateY(-13px) translateX(14px) rotate(5deg)
}
.clouds .cloud--1.cloud-state--3{transform:translateY(-2px) translateX(-10px) rotate(-4deg)
}
.clouds .cloud--1.cloud-state--4{transform:translateY(11px) translateX(-3px) rotate(2deg)
}
.clouds .cloud--1.cloud-state--5{transform:translateY(-10px) translateX(39px) rotate(2deg)
}
.clouds .cloud--1.cloud-state--6{transform:translateY(20px) translateX(-19px) rotate(-4deg)
}
.clouds .cloud--1.cloud-state--7{transform:translateY(2px) translateX(9px) rotate(2deg)
}
.clouds .cloud--1.cloud-state--8{transform:translateY(0px) translateX(-24px) rotate(3deg)
}
.clouds .cloud--1.cloud-state--9{transform:translateY(20px) translateX(-3px) rotate(-2deg)
}
.clouds .cloud--2{background-image:url("./../assets/nuage2.png")
}
.clouds .cloud--2.cloud-state--1{transform:translateY(-1px) translateX(-20px) rotate(2deg)
}
.clouds .cloud--2.cloud-state--2{transform:translateY(-13px) translateX(3px) rotate(4deg)
}
.clouds .cloud--2.cloud-state--3{transform:translateY(21px) translateX(3px) rotate(-1deg)
}
.clouds .cloud--2.cloud-state--4{transform:translateY(12px) translateX(-32px) rotate(-2deg)
}
.clouds .cloud--2.cloud-state--5{transform:translateY(7px) translateX(16px) rotate(1deg)
}
.clouds .cloud--2.cloud-state--6{transform:translateY(14px) translateX(8px) rotate(-2deg)
}
.clouds .cloud--2.cloud-state--7{transform:translateY(20px) translateX(9px) rotate(-2deg)
}
.clouds .cloud--2.cloud-state--8{transform:translateY(-19px) translateX(-39px) rotate(-2deg)
}
.clouds .cloud--2.cloud-state--9{transform:translateY(-16px) translateX(12px) rotate(3deg)
}



