
.range-slider__limit{position:absolute;top:0px;font-size:25px
}
.range-slider__start{left:-70px
}
.range-slider__end{right:-70px
}
*,*:before,*:after{box-sizing:border-box
}
body{font-family:sans-serif;padding:60px 20px
}
@media (min-width: 600px){
body{padding:60px
}
}
.range-slider{margin:0
}
.range-slider{width:100%
}
.range-slider__container{width:100%;position:fixed;bottom:0;left:0;padding:45px 15%;z-index:999;height:100px;box-sizing:border-box;border-top:2px solid black;background-color:white
}
.range-slider__range{-webkit-appearance:none;width:100%;height:5px;border-radius:5px;background:#000;outline:none;padding:0;margin:0
}
.range-slider__range::-webkit-slider-thumb{-webkit-appearance:none;appearance:none;width:20px;height:20px;border-radius:50%;background:#9cc6f0;cursor:pointer;transition:background .15s ease-in-out
}
.range-slider__range::-webkit-slider-thumb:hover{background:#70ade9
}
.range-slider__range:active::-webkit-slider-thumb{background:#70ade9
}
.range-slider__range::-moz-range-thumb{width:20px;height:20px;border:0;border-radius:50%;background:#9cc6f0;cursor:pointer;transition:background .15s ease-in-out
}
.range-slider__range::-moz-range-thumb:hover{background:#70ade9
}
.range-slider__range:active::-moz-range-thumb{background:#70ade9
}
.range-slider__value{display:inline-block;position:relative;width:60px;color:#fff;line-height:20px;text-align:center;border-radius:3px;background:#9cc6f0;padding:5px 10px;margin-left:8px
}
.range-slider__value:after{position:absolute;top:8px;left:-7px;width:0;height:0;border-top:7px solid transparent;border-right:7px solid #9cc6f0;border-bottom:7px solid transparent;content:''
}
::-moz-range-track{background:#000;border:0
}
input::-moz-focus-inner,input::-moz-focus-outer{border:0
}
@media screen and (max-width: 1360px){
.range-slider__container{padding:30px 15%;height:80px
}
}



